<template>
    <div>
        <top-nav title="顾问申请列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是申请分销代理列表</p>
            <p>2).可以选择通过或拒绝申请，通过的申请会出现在代理列表中</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form class="search-form" :inline="true" size="mini">
                    <el-form-item label="关键字">
                        <el-input v-model="keyword" size="mini" placeholder="输入关键字"></el-input>
                    </el-form-item>
                    <el-form-item label="状态">
                        <el-select size="mini" v-model="status" placeholder="选择状态">
                            <el-option label="全部状态"  :value="-999"></el-option>
                            <el-option label="等待审核"  :value="0"></el-option>
                            <el-option label="审核通过"  :value="1"></el-option>
                            <el-option label="审核拒绝"  :value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="mini" @click="onSearch">搜索</el-button>
                        <el-button type="text" @click="keyword='',status=-999,onSearch(0)">取消搜索</el-button>
                    </el-form-item>
                </el-form>

                <el-table :data="datas" style="width: 100%;margin-top:20px;">
                    <el-table-column label="ID" prop="apply_id"></el-table-column>

                    <el-table-column label="申请用户" >
                        <template slot-scope="scope">
                            用户ID: {{scope.row.user_id}}<br/>
                            昵称: {{scope.row.nickname}}
                        </template>
                    </el-table-column>

                    <el-table-column label="联系方式" prop="mobile"></el-table-column>

                    <el-table-column label="头像">
                        <template slot-scope="scope">
                            <img  width="30" :src="scope.row.face" />
                        </template>
                    </el-table-column>

                    <el-table-column label="状态">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.status == 1" type="success">审核通过</el-tag>
                            <el-tag v-if="scope.row.status == 2" type="warning">审核拒绝</el-tag>
                            <el-tag v-if="scope.row.status == 0" type="info">等待审核</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column label="备注" prop="reason"></el-table-column>

                    <el-table-column label="当前状态" prop="reason"></el-table-column>

                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <router-link v-if="scope.row.status==0" :to="'/agent/apply/view/'+scope.row.apply_id">审核</router-link>
                        </template>
                    </el-table-column>
                </el-table>

                <br/>
                <div class="page">
                    <div class="page-tool"></div>

                    <div class="page-mian">
                        <el-pagination @current-change="getLists" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'applyList',
        components: {TopNav},
        data: function() {
            return {
                loading:false,
                keyword:'',
                status:'',
                page:1,
                limit:10,
                count:0,

                datas: [

                ],
            };
        },
        created() {
            this.getLists();
        },
        methods: {

            setPage:function(res){
                this.page = res;
                this.getLists();
            },

            getLists:function(){
                this.loading = true;
                api.post('shop/admin.agent.agentapply/getLists',{
                    appToken: this.$store.getters.appToken,
                    keyword:this.keyword,
                    page:this.page,
                    limit:this.limit,
                    status:this.status,
                }).then(res=>{
                    this.loading = false;
                    this.datas = res.list;
                    this.limit = res.limit;
                    this.count = res.count;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            onSearch:function(){
                this.page = 0;
                this.getLists();
            },
        }
    }
</script>
